
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
}
.col-md-3,
.col-sm-6 {
  text-align: center;
  padding: 5% 0;
}

.col-md-3 img,
.col-sm-6 img {
  max-width: 100%;
  width: 90%;
}

.searchbox:focus {
  outline:0;
  color: white;
}

.searchbox {
  border: none;
  border-radius: 12px;
  padding-left: 8px;
  background: black;
  text-align: right;
}

.search-option-header {
  display: inline-block;
}

.search-options {
  font-size: 1.2em;
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s;
  border: 2px solid white;
  text-transform: capitalize;
  border-radius: 12px;
  padding: 0.3em 1em;
  margin: 0 5px;
}

.search-options:hover{
  color: teal;
  border: 2px solid teal;
}

.load-more-btn {
  position: fixed;
  top: 50%;
  width: 30px;
  background: none;
  border: none;
}

.load-more-btn:hover {
  cursor: pointer;
}

.load-more-btn:focus,
.searchbox:focus {
  outline:0;
}

.load-more-btn span {
  font-size: 40px;
  color: white;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.load-more-btn span:hover {
  font-size: 40px;
  color: teal;
}

.load-more-btn.right {
  position: fixed;
  right: 5%;
}

.load-more-btn.left {
  position: fixed;
  left: 5%;
}

.backdrop {
  background-size: cover;
}

.detail-title-container {
  padding-bottom: 5%;
}

.release-date {
  font-size: 24px;
  padding-left: 10px;
  opacity: 0.8;
}

.profile-outter {
  text-align: center;
}

.cast-name {
  font-size: .8rem;
}

.profile-pic {
  max-width: 100%;
  border-radius: 12px;
}

.reset-padding {
  padding-top: 0;
  padding-bottom: 0;
}

.play-trailer {
  cursor: pointer;
}

.fa-youtube {
  font-size: 32px;
}

@-webkit-keyframes modal-video-inner {
	from {
		-webkit-transform: translate(0, 100px);
		        transform: translate(0, 100px);
	}

	to {
		-webkit-transform: translate(0, 0);
		        transform: translate(0, 0);
	}
}

@keyframes modal-video-inner {
	from {
		-webkit-transform: translate(0, 100px);
		        transform: translate(0, 100px);
	}

	to {
		-webkit-transform: translate(0, 0);
		        transform: translate(0, 0);
	}
}

.modal-video {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .5);
	z-index: 1000000;
	cursor: pointer;
	opacity: 1;
	-webkit-animation-timing-function: ease-out;
	        animation-timing-function: ease-out;
	-webkit-animation-duration: .3s;
	        animation-duration: .3s;
	-webkit-animation-name: modal-video;
	        animation-name: modal-video;
	-webkit-transition: opacity .3s ease-out;
	transition: opacity .3s ease-out;
}


.modal-video-movie-wrap {
  position: absolute;
  top: 80px;
  left: 80px;
  right: 80px;
  bottom: 80px;
	background-color: #333;
	-webkit-animation-timing-function: ease-out;
	        animation-timing-function: ease-out;
	-webkit-animation-duration: .3s;
	        animation-duration: .3s;
	-webkit-animation-name: modal-video-inner;
	        animation-name: modal-video-inner;
	-webkit-transform: translate(0, 0);
	transform: translate(0, 0);
	-webkit-transition: -webkit-transform .3s ease-out;
	transition: -webkit-transform .3s ease-out;
	transition: transform .3s ease-out;
	transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}

.modal-video-movie-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal-video-close-btn {
	position: absolute;
	z-index: 2;
	top: 4px;
	right: -22px;
	display: inline-block;
	width: 35px;
	height: 35px;
	overflow: hidden;
	border: none;
  background: transparent;
}

.modal-video-close-btn:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.modal-video-close-btn:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.modal-video-close-btn:before,
.modal-video-close-btn:after {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #fff;
  border-radius: 5px;
  margin-top: -6px;
}

.play-trailer-btn,
  button.go-back {
  background-color: black;
  border: none;
}

.play-trailer-btn:hover{
  cursor: pointer;
}

.play-trailer-btn svg {
  color: white;
}

a.go-back {
  color: white;
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s;
  position: absolute;
  top: 30px;
  left: 30px;
}

a.go-back:hover {
  color: teal;
}

.play-trailer-btn:focus,
a.go-back:focus,
.modal-video-close-btn:focus {
  outline: none;
}

/* RATING CONTAINER */
.rating-container {
  position: relative;
}

.rating-container p {
  position: absolute;
  width: 100px;
  color: white;
  text-decoration: none;
  border-bottom: none;
  bottom: 46px;
  left: 26px;
  font-weight: light;
  font-size: 12px;
  margin: 0;
}


.rating-container .fa-star {
  position: absolute;
  top: 22px;
  right: 40px;
  font-size: 38px;
}

.rating-container #rating-score {
  position: absolute;
  font-size: 19px;
  right: 46px;
  bottom: 22px;
}

circle {
  -webkit-transition: stroke-dashoffset 0.55s;
  transition: stroke-dashoffset 0.55s;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

.movie-info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.padding-b {
  padding-bottom: 10px;
}

.movie-detail-poster img {
  max-width: 100%;
}

.cast-outter {
  min-height: 222px;
}

.crew-label {
  margin: 0;
}

.crew-col {
  width: 20%;
  padding: 0 10px;
  min-height: 222px;
}

.crew-img {
  max-height: 174px;
}

@media (max-width: 575.98px) {
  .search-options {
    font-size: 0.8em;
  }
  .search-option-header {
    font-size: 1.2em;
  }
  .movie-detail-poster {
    padding: 5% 5%;
  }
  .align-center-sm {
    text-align: center;
  }

  .rating-container p {
    left: 14px;
  }

  .crew-col {
    width: 30%;
    min-height: 200px;
    padding: 0 10px;
  }

  .profile-outter {
    max-width: 25%;
    margin-bottom: 15px;
  }

  .cast-outter {
    min-height: 140px;
  }


  .crew-label {
    font-size: 0.8em;
  }
 }



 @media (max-width: 1199.98px) {
  a.go-back {
    top: 10px;
    left: 15px;
  }
}
